<template>
  <div class="countdown">
    <div class="now h3 text-primary">{{ now }}</div>

    <div class="container">
      <div class="container-item" v-for="(value, key) in timeUntil" :key="key">
        <span v-if="value > 0" class="btn badge" :data-badge="key">
          <span class="h3">{{ value }}</span>
        </span>
      </div>
    </div>
  </div>
</template>

<script>
import moment from 'moment'

export default {
  name: 'CountDown',
  data() {
    return {
      now: this.currentTime(),
      timeUntil: this.GetTimeUntil(),
      dateTo: moment('2021-03-29 17:35')
    }
  },
  methods: {
    currentTime: function() {
      return moment().format('MMMM Do YYYY')
    },
    GetTimeUntil: function() {
      var time = {
        days: moment(this.dateTo).diff(moment(), 'days'),
        hours: moment(this.dateTo).diff(moment(), 'hours'),
        minutes: moment(this.dateTo).diff(moment(), 'minutes'),
        seconds: moment(this.dateTo).diff(moment(), 'seconds')
      }
      return time
    }
  },
  mounted() {
    this.interval = setInterval(() => {
      this.now = this.currentTime()
      this.timeUntil = this.GetTimeUntil()
    }, 1000)
  },
  destroyed() {
    clearInterval(this.interval)
  }
}
</script>

